import api from './index.js'

export default {
  async getServicesOrders () {
    const res = api().get('/api/services-orders')
    return res
  },
  async updateserviceOrder (data,  id) {
    const res = api().put(`/api/services-orders/${id}`,data)
    return res
  },
  async updatePayment (data,  id) {
    const res = api().post(`/api/services-orders/${id}/paid-cash`,data)
    return res
  }
}
