<template>
   <div class="table-responsive" >
		<v-data-table
			:headers="headers"
			:items="invoiceData"
			:items-per-page="15"
			:search="search"
			align='center'
		>
        <template v-slot:item.paid="{ item }" >
            <v-btn small tile elevation="0" @click="changePaidStatus(item,item.id)" class="primary">{{item.paid}}</v-btn>
			</template>
			<template v-slot:item.action="{ item }" >
                <v-select class="text-xs-sm" @change="updateService(item.id, item.status)"  :items="Departments" 
							v-model="item.status" dense></v-select>
			</template>
			
		</v-data-table>
		<emb-delete-confirmation-2
			ref="deleteConfirmationDialog"
			messageTitle="Are You Sure You Want To Delete?"
			messageDescription="Are you sure you want to delete this invoice permanently?"
			@onConfirm="ondeleteItemFromInvoicesList"
			btn1="Cancel"
			btn2="Yes"
		>
		</emb-delete-confirmation-2>
	</div>
</template>
<style  scoped>
#products{
	display: flex;
	justify-content: space-between;
	/* text-align: center; */
	align-items: center;
	margin-top: 10px;
}
#tittle{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
/* #producr{
	display: flex;
	justify-content: space-between;
} */
</style>
<script>
import serviceOrders from "Api/serviceOrders";
import moment from "moment"
import { mapGetters } from "vuex";
export default {
	computed: {
			...mapGetters(["linksformbackend"])
		},
  props: {
    search: {
      default: null,
      type: String
    }
  },
  data() {
    return {
        Departments: [
            'SENT',
            'RECEIVED',
            'CANCELED',
            'TECHNICIAN-ON-THE-WAY',
            'TECHNICIAN-AT-SITE',
            'JOB-DONE'
        ],
      editDialog: false,
      invoiceList: null,
      headers: [
		{
          text: "Date",
          sortable: false,
          value: "date"
        },
		{
          text: "First Name",
          sortable: false,
          value: "firstName"
		},
		{
          text: "Last Name",
          sortable: false,
          value: "lastName"
        },
        {
          text: "Email",
          sortable: false,
          value: "email"
		},
		// {
        //   text: "Street Number",
        //   sortable: false,
        //   value: "streetNumber"
        // },
		{
          text: "Address",
          sortable: false,
          value: "address"
		},
        {
          text: "Paid",
          sortable: false,
          value: "paid"
        },
        {
          text: "Services",
          sortable: false,
          value: "services"
        },
		{
          text: "Status",
          sortable: false,
          value: "action"
        },
      ],
      selectDeletedItem: null,
	  invoiceData: [],
	selectProduts: [],
	pictures:'',
	name: '',
	total: ''
    };
  },
  async created() {
	// this.getInvoice();

	try {
		const res = await serviceOrders.getServicesOrders()
		console.log(res)
		res.data.data.forEach(el => {
            // console.log(el)
            const dat = moment(el.updatedAt)
			const time = moment(el.updatedAt)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
            this.invoiceData.push({
                id: el._id,
                date: date,
                firstName:el.firstName,
                lastName:el.lastName,
                paid: el.paid,
                email:el.email,
                address: el.address,
                services:el.service.name.en,
                status:el.status
            })
        })
	} catch (err) {
		console.log(err)
	}
  },
  methods: {
    async updateService(id, status){
        try {
            const res = await serviceOrders.updateserviceOrder({status: status}, id)
            // console.log(res)
             this.$snotify.success(`${res.data.message}`,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 3000,
					showProgressBar:false,
				});

        } catch (err) {
            console.log(err.message)
        }
    },
    async changePaidStatus(order,id){
        try {
            console.log(order)
            if (order.paid === true) {
                order.paid = false
            }else{
                order.paid = true
            }
            const res = await serviceOrders.updatePayment({paid: order.paid},id)
               this.$snotify.success(`${res.data.message}`,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 3000,
					showProgressBar:false,
				});
        } catch (err) {
            console.log(err.message)
        }
    }
  }
};
</script>
